pre,
code {
    font-family: $font-code;
    font-size: 1rem;
}
code {
    padding: 1px 5px;
}

pre {
    padding: 20px 15px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}

.highlight {
    border-radius: 3px;
    background: $color-bckgd__light;

    .highlighter-rouge & {
        background: $color-bckgd__light;
    }
}

.highlight {
    pre {
        background-color: #272822;
    }
    .hll {
        background-color: #272822;
    }
